<template>
	<ul>
		<template v-for="(item,index) of list">
			<li @click="showMyChildren($event,index)"  :class="{'has':item.children}"  :ref="index" >
			<span v-if="item.children">{{item.title}}</span>
			<router-link v-else to="/Goods/detail" tag="span">{{item.title}}</router-link>
			<detail-list  v-if="item.children" :list="item.children" class="submenu"></detail-list>
			</li>
			<detail-list v-if="item.children" :list="item.children" class="submenu"></detail-list>

		</template>
	</ul>
</template>
<script>
	export default {
		name:"detailList",
		props:{list:Array},
		methods:{
			showMyChildren:function(e,index){
				e.stopPropagation()
				this.$refs[index][0].classList.toggle("expand")
				// this.$refs[index][0].nextElementSibling.style.display="block"
			}
		},
		mounted (){

		}
	}
</script>
<style scoped>
	@import url("../../assets/css/goodsCategory.css");
/*
	 .menu > li{
	     box-sizing: border-box;
	     padding: 0 0.14rem;
	     padding-left: 0.34rem;
	     margin-bottom: 0.01rem;
	     line-height: 0.44rem;
	     white-space: nowrap;
	     text-overflow: ellipsis;
	     background-color: #fff;
	     overflow: hidden;
	}
	 .menu > li span{
	     vertical-align: middle;
	}
	 .menu .has:before{
	     content: "";
	     display: inline-block;
	     width: 0.16rem;
	     vertical-align:middle;
	     height: 0.16rem;
	     margin-right: 0.1rem;
	     margin-left: -0.2rem;
	     background: #fff url(../img/right.png) no-repeat;
	     background-size: 0.16rem;
	}
	 .menu .has.expand:before{
	     background-color: #f9f9f9;
	     -webkit-transform: rotate(90deg);
	     -ms-transform: rotate(90deg);
	     -o-transform: rotate(90deg);
	     transform: rotate(90deg);
	}
	 .submenu{
	     display: none;
	}
	 .submenu > li{
	     text-indent: 1em;
	     line-height: 0.4rem;
	}
	 .submenu > li > a{
	     display: block;
	}
	 .expand .submenu{
	     display: block;
	     padding-bottom: 0.1rem;
	}*/
</style>


